<template>
<div>

          <h1 class="title">{{$t('login.title')}}</h1>
          <div class="field">
            <p class="control">
              <input class="input" v-model="userEmail" type="email" placeholder="Email">
            </p>
          </div>
          <div class="field">
            <p class="control">
              <input class="input" v-model="userPassword" v-on:keyup.enter="doLogIn" type="password" :placeholder="'Password'">
            </p>
          </div>
          <div class="field">
            <a @click="doRecoverPassword" class="has-text-dark">{{$t('login.forgotPassword')}}</a>
          </div>
          <div class="buttons is-right">
            <button @click="doSignUp" class="button is-white">
            {{$t('login.signupButton')}}
            </button>
            <button @click="doLogIn" class="button is-primary"  :disabled="loginLoading || !userPassword || !userEmail" :class="{'is-loading': loginLoading}">
            {{$t('login.loginButton')}}
            </button>
          </div>
          <div class="notification is-warning" v-if="loginFailed">
            <button class="delete" @click="loginFailed = false"></button>
            {{$t('login.loginWarning')}}
            </div>
</div>
</template>

<script>
export default {
  data(){
    return {
      // REQUIRED
      userEmail: null,
      userPassword: null,
      userEmailRecover: null,
      loginLoading: false,
      loginFailed: false
    }
  },
  methods: {
    doSignUp() {
      this.$emit('showSignUp')
    },
    doRecoverPassword() {
      this.$emit('showRecoverPassword')
    },
    doLogIn() {
      this.loginLoading = true
      this.loginFailed = false
      if (this.userEmail && this.userPassword) {
        this.$http.post('/user/login', {
          email: this.userEmail.toLowerCase(),
          password: this.userPassword,
        })
          .then(() => {
            const urlParams = new URLSearchParams(window.location.search);
            const redirectUrl = urlParams.get('redirect');
            if(redirectUrl){
              window.location.replace(redirectUrl);
            }
            else {
              window.location.replace('/profile');
            }
          })
          .catch((err) => {
            console.error(err)
            this.loginFailed = true
          })
          .finally(() => {
            this.loginLoading = false
          });
      }
    }
  }
}
</script>